<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>种植信息管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:plainfo:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:plainfo:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:planinfo:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:plainfo:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <div class="form-group">
                        <label class="control-label col-sm-2">大棚编号</label>
                        <div class="col-sm-6">
                            <select type="text" name="greenhouse" class="form-control" v-model="addData.greenhouse_id">
                                <option :value="item.greenhouseId" v-for="(item,index) of greenhouse" :key="index" v-text="item.greenHouseId"></option>
                                 </select>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">作物类型id</label>
                        <div class="col-sm-6">
                            <select type="text" name="croptype" class="form-control" v-model="addData.crop_varieties_id">
                                <option :value="item.croptypeId" v-for="(item,index) of croptype" :key="index" v-text="item.cropVarietiesId"></option>
                                 </select>
                        </div>
                    </div>
                   
                   
                                <div class="form-group">
                                    <label class="control-label col-sm-2">备注</label>
                                    <div class="col-sm-6" :class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}">
                                        <textarea rows="6" class="form-control" name="remark" :placeholder="'请输入备注'" v-model="addData.remark" :data-vv-as="'备注'" v-validate="'required'"></textarea>
                                    </div>
                                    <div class="text-danger">
                                        <span v-show="errors.has('remark')">{{errors.first("remark")}}</span>
                                    </div>
                                </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
种植信息管理
<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "",
                pageloginData: {
                    id: this.stateid
                },
                filterName: [],
                
            },
            greenhouse:[],
            croptype:[],
            addData: {
                greenhouse_id:"",
                crop_varieties_id:"",
                remark:""
            }
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    methods: {
        //大棚信息获取
         getgreenhouse_id() {
            var vm = this;
            vm.$axios
                .post(vm.HOST + "")
                .then(function (data) {
                    if (data.bool) {
                        vm.greenhouseid = data.data;
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        //获取作物类型ID
         getcroptype() {
            var vm = this;
            vm.$axios
                .post(vm.HOST + "")
                .then(function (data) {
                    if (data.bool) {
                        vm.croptype = data.data;
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        //添加按钮按下
        inforAddClick(bool) {
            var vm=this;
            var opreate=this.opreate;
            //修改状态下
            // if(opreate=='update'){
            //     this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]); //将选择中行的数据复制给addData，让form表单数据不为空
            // }
            // 异步函数，防止表单出现后存在错误，更改用户体验
            async function Empty(){  //将addData数据修改为空
                   for (var key in vm.addData) {
                    vm.addData[key] = "";
                    }
                    return true;
            } 
          Empty().then(()=>{
            if(vm.$validator.errors.items.length>0){
                vm.$validator.errors.clear() // 清除表单错误
            }
            this.opreate = "add";//将当前操作改为增加
             vm.inforadmin = bool; // 更改显示，根据bool确定
          })
        },
        
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {//判断当前选中数据是否只有一条
                this.inforadmin = true; //更改显示，
                this.opreate = "update";//将当前操作更改为修改
                this.addData =this.$api.opreate.ObjectCopy(this.checkData[0]);//将选择中行的数据复制给addData，让form表单数据不为空
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;  // form表单的绑定数据
            var datas = {      // 向数据接口传输的数据
                data: data,
                states: {
                    id: stateId
                }
            };
            // 异步修改操作 
            async function addDataFun(){
             var bool;
            if (vm.opreate == "update") {
               bool = vm.$api.http.opreateHttp(vm, "", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                 bool = vm.$api.http.opreateHttp(vm, "", datas, "添加", "");
            }
            return bool;
            }
            addDataFun().then(function (success) {
                vm.$validator.errors.clear();
                if (success) {
                    setTimeout(function () {
                        vm.inforAddClick(false);
                    }, 800);
                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA; // 获取当前页面的
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id, //需要删除行的数据id，需要根据行id进行相应修改 如选中行数据id为 userid,则此处需要改为checkData[0].userid
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景类型名后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



